.HomePage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .tableContainer {
    height: calc(-440px + 100dvh);
    overflow-y: auto; /* Bara de derulare vertical */
  }
}

@media screen and (min-width: 1280px) {
  .HomePage {
    display: inline-flex;
    justify-content: center;
    align-self: center;
  }
  .tableContainer {
    height: calc(-220px + 100dvh);
    margin-left: 100px;
  }
}

/* Stilizare pentru scrollbar n Google Chrome i Safari */

::-webkit-scrollbar {
  width: 10px; /* Limea scrollbarului */
}

::-webkit-scrollbar-track {
  background: transparent; /* Fundalul track-ului scrollbarului */
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(
    270.02deg,
    #2e1746 3.2%,
    #2e225f 99.98%
  ); /* Culoarea thumb-ului (butonul) scrollbarului */
  border-radius: 5px; /* Rotunjirea colurilor thumb-ului */
}

::-webkit-scrollbar-thumb:hover {
  background: #555; /* Culoarea thumb-ului la hover */
}

/* Stilizare pentru scrollbar n Firefox */

.scrollbar {
  scrollbar-width: thin; /* Limea scrollbarului */
}

.scrollbar-track {
  background: #f1f1f1; /* Fundalul track-ului scrollbarului */
}

.scrollbar-thumb {
  background: linear-gradient(
    270.02deg,
    #2e1746 3.2%,
    #2e225f 99.98%
  ); /* Culoarea thumb-ului (butonul) scrollbarului */
  border-radius: 5px; /* Rotunjirea colurilor thumb-ului */
}

.scrollbar-thumb:hover {
  background: #555; /* Culoarea thumb-ului la hover */
}
